import React from 'react';
import { Grid, Typography, Box } from '@mui/material';
import EngineeringOutlinedIcon from '@mui/icons-material/EngineeringOutlined';
import AllInclusiveIcon from '@mui/icons-material/AllInclusive';
import PaidOutlinedIcon from '@mui/icons-material/PaidOutlined';
import Cloud from '@mui/icons-material/Cloud';
import SupervisedUserCircle from '@mui/icons-material/SupervisedUserCircle';
import LightbulbOutline from '@mui/icons-material/LightbulbCircleOutlined';
import useStyles from '../styles/styles';
import Slide from '@mui/material/Slide';

const Section = (props) => {
  const classes = useStyles();

  const sectionItems = [
    {
      id: 1,
      icon: <Cloud sx={{ fontSize: 100 }} color="primary" />,
      sentence:
        'Meilt lytyy pitkn linjan kokemusta sulautetuista jrjestelmist  oli kyseess sitten laiteohjaus, datan keruu tai lykkt ratkaisut. Suunnittelemme ja toteutamme juuri sinun tarpeisiisi rtlidyt ratkaisut, jotka kestvt ajan hammasta ja tukevat liiketoimintaasi.',
    },
    {
      id: 2,
      icon: <SupervisedUserCircle sx={{ fontSize: 100 }} color="primary" />,
      sentence:
        'Olemme elektroniikan parissa kuin kotonamme. Vuosien varrella olemme suunnitelleet kaikenlaista piireist ja laitteista aina tuotantoon asti. Yhdistmme teknisen osaamisen maalaisjrkeen, jotta ratkaisut eivt vain toimi, vaan toimivat fiksusti.',
    },
    {
      id: 3,
      icon: <LightbulbOutline  sx={{ fontSize: 100 }} color="primary" />,
      sentence: 'Tekniikka on meille tuttua, mutta se ei riit  haluamme ymmrt mys sinun liiketoimintasi. Yhdess luomme ratkaisuja, jotka istuvat yrityksesi arkeen ja vievt sit eteenpin. Teknologiaa, joka toimii niin kuin pit.',
    },
  ];
  
  return (
    <Box sx={{ flexGrow: 1, minHeight: '400px' }}>
      <Grid container className={classes.sectionGridContainer}>
        {sectionItems.map((item) => (
          <Slide direction="left" in={props.slide} timeout={1000} key={item.id}>
          <Grid
            item
            xs={12}
            md={3.5}
            minHeight={300}
            key={item.id}
            className={classes.sectionGridItem}
          >
            {item.icon}
            <Typography>{item.sentence}</Typography>
          </Grid>
          </Slide>
        ))}
      </Grid>
    </Box>
  );
};

export default Section;
